import { Helmet } from 'react-helmet';
import React from 'react';
import { Alert, Container } from 'reactstrap';

import './Projects.scss';

const Projects = (props) => {

  return (
    <React.Fragment>
      <Helmet>
        <title>AharrFaris || Projects</title>
      </Helmet>

      <section id="Projects">

        <Container>
          <Alert color="success" className="text-center">Have not done anything that interesting yet to show!</Alert>
        </Container>

      </section>
    </React.Fragment>
  );
};

export default Projects;